    	private _settingAttributes: boolean = false;
	                                protected _attached: boolean = false;
	                                    
	                                private _queuedSetAttributes: { attrName: string, attrValue: string }[] = [];
	                                protected _enqueueSetAttribute(attrName: string, attrValue: string) {
	                                     this._queuedSetAttributes.push({ attrName: attrName, attrValue: attrValue });
	                                }
	
	                                protected _flushQueuedAttributes() {
	                                    this._settingAttributes = true;
	                                    for (let i = 0; i < this._queuedSetAttributes.length; i++) {
	                                        this.setAttribute(
	                                            this._queuedSetAttributes[i].attrName,
	                                            this._queuedSetAttributes[i].attrValue);
	                                    }
	                                    this._settingAttributes = false;
	                                    this._queuedSetAttributes.length = 0;
	                                }
	                                protected _a(attrName: string, attrValue: any) {
	                                    if (this._updatingFromAttribute) {
					return;
		    }
	                                    if (attrValue) {
	                                        attrValue = attrValue.toString();
	                                    }
	                                    this._settingAttributes = true;
	                                    attrName = toSpinal(attrName);
	                                    if (this._attached) {
	                                        this.setAttribute(attrName, attrValue);
	                                    } else { 
	                                        this._enqueueSetAttribute(attrName, attrValue);
	                                    }
	                                    this._settingAttributes = false;
	    }

    	
